<template>
  <header id="header" class="header-top">
    <div class="container">
      <h1>
        <router-link class="nav-link" to="/">{{ homeInfo.fullName }}</router-link>
      </h1>

      <nav id="navbar" class="navbar" ref="navbar">
        <ul>
          <li v-for="address in homeInfo.pagesAddress" :key="address.title">
            <router-link
                class="nav-link"
                :class="activeLink(address.route)"
                :to="address.route">
              {{ address.title }}
            </router-link>
          </li>
        </ul>
        <i class="bi bi-list mobile-nav-toggle" @click="navbarToggle"></i>
      </nav>
    </div>
  </header>
</template>

<script>

export default {
  name: "TheHeader",
  props: ['currentPageRoute'],
  data() {
    return {
      homeInfo: {}
    };
  },
  methods: {
    activeLink(route) {
      return route === this.currentPageRoute ? 'active' : '';
    },
    navbarToggle(event) {
      this.$refs.navbar.classList.toggle('navbar-mobile');
      event.target.classList.toggle('bi-list');
      event.target.classList.toggle('bi-x');
    }
  },
  mounted() {
    this.homeInfo = this.$store.getters.homePageInfo;
  }
}
</script>

